import React from 'react';
import PropTypes from 'prop-types';
import Shipment from './Shipment';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

class Order extends React.Component {

  static propTypes = {
    burgers: PropTypes.object,
    order: PropTypes.object,
    deleteFromOrder: PropTypes.func
  }

  renderOrder = (key) => {
    const { burgers, order } = this.props;

    const burger = burgers[key];
    const count = order[key];

    const isAvaliable = burger &&  burger.status === 'available';

    const transitionOptions = {
      classNames: "order", 
      key,
      timeout: {enter: 500, exit: 500}
    }

    if (!burger) return null;

    if (!isAvaliable) {
      return (
      <CSSTransition 
        {...transitionOptions}
      >
        <li className="unavailable" key={key}>
          –ò–∑–≤–∏–Ω–∏—Ç–µ, {burger ? burger.name : '–±—É—Ä–≥–µ—Ä'} –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ –¥–æ—Å—Ç—É–ø–µ–Ω
        </li>
      </CSSTransition>
      );
    }

    return (
    <CSSTransition 
      {...transitionOptions}
    >
      <li key={key}>
        <span>
          <TransitionGroup component="span" className="count">
            <CSSTransition classNames="count" key={count} timeout={{enter: 500, exit: 500}}>
              <span>{count}</span>
            </CSSTransition>
          </TransitionGroup>
          

          —à—Ç. {burger.name}
          <span> {count * burger.price} ‚ÇΩ</span>
        </span>
  
        <button 
          onClick={() => this.props.deleteFromOrder(key)}
          className="cancellItem"
        >&times;</button>
      </li>
    </CSSTransition>
    );
          
  };

  render() {
    const { burgers, order } = this.props;
    const orderIds = Object.keys(order);
    const total = orderIds.reduce((prevTotal, key) => {
      const burger = burgers[key];
      const count = order[key];

      const isAvaliable = burger && burger.status === 'available';
      if (isAvaliable) {
        return prevTotal + burger.price * count;
      }
      return prevTotal;
      
    }, 0)

    return(
      <div className="order-wrap">
        <h2>–í–∞—à –∑–∞–∫–∞–∑</h2>
        <TransitionGroup 
          component='ul' 
          className="order"
        >
          {orderIds.map(this.renderOrder)}
        </TransitionGroup>
        {total > 0 ? (
          <Shipment total={total} />
          )
          : 
          (<div className="nothingSelected">
            –í—ã–±–µ—Ä–µ—Ç–µ –±–ª—é–¥–∞ –∏ –¥–æ–±–∞–≤—å—Ç–µ –∫ –∑–∞–∫–∞–∑—É
          </div>)

        }
      </div>
    )
  }
}

export default Order;